import {DisplayObject} from "../core/DisplayObject";

/**
 * 
 * 
 * checkGroup 
 * 
 * @example let container = new gui.Container();
 * 
 * @namespace gui
 * 
 * @link https://vipkid-edu.github.io/vf-gui/play/#example/TestContainer
 */
export class Container extends DisplayObject{
    public constructor(){
        super();
        this.isContainer = true;
    }

    /**
     *  DisplayObjectContainer  DisplayObjectContainer 
     *  true
     * @param child 
     * @returns  child  DisplayObjectContainer  true false
     */
    public contains(child: DisplayObject){
        while (child) {
            if (child == this) {
                return true;
            }
            if(child.parent instanceof DisplayObject){
                child = child.parent;
            }
            return false;
            
        }
        return false;
    }

}