import { Tween } from "../Tween";

/**
 * 
 * @private
 */
const _tweens: Tween[] = [];
/**
 * 
 */
let isStarted = false;
/**
 * 
 */
let emptyFrame = 0;
/**
 * 
 */
let powerModeThrottle = 120;
/**
 * 
 */
let handleLag = true;

/**
 * 
 * @namespace tween.Plugins
 * @memberof gui.tween
 * @example
 * let num = Plugins.num = function (node, start, end) {
  * return t => start + (end - start) * t
  * }
  *
  * @static
  */
export const Plugins:TAny = {};

/**
 * 
 * @param {Tween} tween Tween 
 * @memberof gui.tween
 * @example
 * let tween = new gui.tween.Tween({x:0})
 * tween.to({x:200}, 1000)
 * gui.tween.add(tween)
 */
export function add(tween: Tween) {
    let i = _tweens.indexOf(tween);

    if (i > -1) {
        _tweens.splice(i, 1);
    }

    _tweens.push(tween);

    emptyFrame = 0;

    isStarted = true;
}

/**
 * 
 * @param {number} frameCount=120 
 * @memberof gui.tween
 * @example
 * gui.tween.FrameThrottle(60)
 */
export function FrameThrottle(frameCount = 120) {
    powerModeThrottle = frameCount * 1.05;
}

/**
 * canvasdom
 * @param {number} state=true 
 * @memberof gui.tween
 * @example
 * gui.tween.ToggleLagSmoothing(false)
 */
export function ToggleLagSmoothing(_state = true) {
    handleLag = _state;
}

/**
 * 
 * @memberof gui.tween
 * gui.tween.getAll() 
 */
export function getAll() {
    return _tweens;
}

/**
 * 
 * @example  gui.tween.removeAll()
 * @memberof gui.tween
 */
export function removeAll() {
    _tweens.length = 0;
    isStarted = false;
}

/**
 * 
 * @param {Tween} tween  
 * @return {Tween} null
 * @memberof gui.tween
 * @example
 * gui.tween.get(tween)
 */
export function get(tween: Tween) {
    for (let i = 0; i < _tweens.length; i++) {
        if (tween === _tweens[i]) {
            return _tweens[i];
        }
    }
    return null;
}

export function removeDisplay(uuid:string){
    for (let i = 0; i < _tweens.length; i++) {
        if (_tweens[i].object.uuid && uuid === _tweens[i].object.uuid) {
            _tweens[i].stop();
            remove(_tweens[i]);
            return;
        }
    }
    return;
}

/**
 * 
 * @param {Tween} tween Tween instance
 * @memberof gui.tween
 * @example
 * gui.tween.remove(tween)
 */
export function remove(tween: Tween) {
    const i = _tweens.indexOf(tween)
    if (i !== -1) {
        _tweens.splice(i, 1)
    }
    if (_tweens.length === 0) {
        isStarted = false;
    }
}

/**
 * 
 * @param {number=} time 
 * @param {Boolean=} preserve 
 * @memberof gui.tween
 * @example
 * gui.tween.update(500)
 */

export function update(time:number, preserve = false) {
    if (!isStarted) {
        return false;
    }
    if (emptyFrame >= powerModeThrottle && handleLag) {
        console.log("mptyFrame >= powerModeThrottle && handleLag");
        isStarted = false;
        emptyFrame = 0;
        return false;
    }
    
    if (!_tweens.length) {
        emptyFrame++;
    }

    let i = 0;
    let length = _tweens.length;
    while (i < length) {
        _tweens[i++].update(time, preserve);

        if (length > _tweens.length) {
            // The tween has been removed, keep same index
            i--;
        }

        length = _tweens.length;
    }

    return true;
}

/**
 * 
 * @return {Boolean} true
 * @memberof gui.tween
 * @example gui.tween.isRunning()
 */
export function isRunning() {
    return isStarted;
}

/**
 * 
 * @return {Boolean} 
 * @memberof gui.tween
 * @example gui.tween.isRunning()
 */
export function isLagSmoothing() {
    return handleLag;
}